/* global window */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import Header from '../../components/Header';
import Loading from '../../components/Loading';
import NewsletterService from '../../services/newsletter';
import styles from './styles';

const defaultState = {
  loading: false,
  content: '',
  title: 'Home'
};

class Issues extends PureComponent {
  static propTypes = {
    classes: PropTypes.object,
    match: PropTypes.object,
    history: PropTypes.object
  };

  constructor(props) {
    super(props);
    this.state = defaultState;
  }

  async componentDidMount() {
    this.loadIssueContent();
  }

  loadIssueContent = async () => {
    this.setState({ loading: true }, async () => {
      try {
        const { match: { params: { id } } } = this.props;
        const issue = await NewsletterService.getIssueContentById(id);
        if (issue) {
          const { title, content } = issue;
          this.setState({ content, title, loading: false });
        } else {
          this.setState(defaultState);
        }
      } catch (err) {
        this.setState(defaultState);
      }
    });
  }

  onBackButtonClick = () => {
    const { history } = this.props;
    history.push('/');
  }

  render() {
    const { classes } = this.props;
    const { loading, content, title } = this.state;
    return (
      <React.Fragment>
        <Header title={title} backButton={this.onBackButtonClick} />
        <main className={classes.layout}>
          {
            loading ?
              <Loading />
              :
              (
                content ?
                  <iframe
                    srcDoc={`<html><body>${content}</body></html>`}
                    title={title}
                    frameBorder={0}
                    width={window.innerWidth}
                    height={window.innerHeight}
                    className={classes.viewer}
                  />
                  :
                  <Grid
                    container
                    direction="column"
                    justify="center"
                    alignItems="center"
                    className={classes.empty}
                  >
                    <Typography variant="h6">
                      Error on load newsletter content
                    </Typography>
                  </Grid>
              )
          }
        </main>
      </React.Fragment>
    );
  }
}

export default withStyles(styles)(Issues);
